<template>
  <div class="demo-popup">
    <div class="demo-title"></div>
    <div class="demo-container">
      <div class="demo-example-margin">
        <sv-button @click.native="test" type="info"></sv-button>
      </div>
    </div>
    <div class="demo-title"></div>
    <div class="demo-container">
      <div class="demo-example-margin">
        <sv-button @click.native="test1" type="info"></sv-button>
      </div>
      <div class="demo-example-margin">
        <sv-button @click.native="test2" type="info"></sv-button>
      </div>
    </div>
    <div class="demo-title"></div>
    <div class="demo-container">
      <div class="demo-example-margin">
        <sv-button @click.native="test3" type="info"></sv-button>
      </div>
      <div class="demo-example-margin">
        <sv-button @click.native="test4" type="info"></sv-button>
      </div>
    </div>
    <div class="demo-title"></div>
    <div class="demo-container">
      <div class="demo-example-margin">
        <sv-button @click.native="test5" type="info"></sv-button>
      </div>
    </div>
    <sv-toast v-model="value"></sv-toast>
  </div>
</template>
<script>
import { mapMutations } from 'vuex'
const route = {
  tags: '',
  label: 'toast',
  path: 'toast'
}
export default {
  ...route,
  data() {
    return {
      value: false
    }
  },
  created() {
    this.commitRoute(route)
  },
  methods: {
    test() {
      this.$toast({
        type: 'icon',
        message:
          ''
      })
    },
    test1() {
      this.$toast({
        type: 'icon',
        icon: 'success',
        message: ''
      })
    },
    test2() {
      this.$toast({
        type: 'icon',
        icon: 'warn',
        message: ''
      })
    },
    test3() {
      this.$toast({
        type: 'loading',
        loadingType: 'circle',
        message: ''
      })
    },
    test4() {
      this.$toast({
        type: 'loading',
        loadingType: 'spot',
        message: ''
      })
    },
    test5() {
      this.value = true
    },
    ...mapMutations({
      commitRoute: 'changeRoute'
    })
  }
}
</script>