<template>
  <div>
    <div class="demo-title"></div>
    <div class="demo-container">
      <sv-button @click.native="alert"></sv-button>
    </div>
    <div class="demo-title"></div>
    <div class="demo-container">
      <div class="demo-example-margin">
        <sv-button type="danger" @click.native="alertType('danger')">danger</sv-button>
      </div>
      <div class="demo-example-margin">
        <sv-button type="primary" @click.native="alertType('success')">success</sv-button>
      </div>
      <div class="demo-example-margin">
        <sv-button type="warning" @click.native="alertType('warning')">warning</sv-button>
      </div>
      <div class="demo-example-margin">
        <sv-button type="info" @click.native="alertType('primary')">primary</sv-button>
      </div>
    </div>
    <div class="demo-title"></div>
    <div class="demo-container">
      <sv-button @click.native="coutrom"></sv-button>
    </div>
    <div class="demo-title"></div>
    <div class="demo-container">
      <div class="demo-example-margin">
        <sv-button type="info" @click.native="alertOfIcon"></sv-button>
      </div>
      <div class="demo-example-margin">
        <sv-button type="primary" @click.native="alertOfIconSuccess"></sv-button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapMutations } from 'vuex'
import Vue from 'vue'
const route = {
  tags: '',
  label: 'notify',
  path: 'notify'
}

export default {
  ...route,
  created() {
    this.commitRoute(route)
  },
  methods: {
    alert() {
      this.$notify({
        duration: 2000,
        message: 'Welcome to here !'
      })
    },
    alertType(type) {
      this.$notify({
        duration: 2000,
        message: '',
        type
      })
    },
    coutrom() {
      this.$notify({
        message: '',
        type: 'success',
        background: '#551A8B',
        color: '#D33682'
      })
    },
    alertOfIcon() {
      this.$notify({
        message: '2',
        type: 'primary',
        icon: 'message'
      })
    },
    alertOfIconSuccess() {
      this.$notify({
        message: '',
        type: 'success',
        icon: 'success'
      })
    },
    ...mapMutations({
      commitRoute: 'changeRoute'
    })
  }
}
</script>